export default function footer() {
    const elPeru = document.getElementById("clock-peru");
    const elLocal = document.getElementById("clock-local");

    // Formateador para Perú usando IANA timezone (más fiable que “sumar/restar horas”)
    const fmtPeru = new Intl.DateTimeFormat("es-PE", {
        timeZone: "America/Lima",
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
        hour12: false
    });

    // Formateador para hora local del usuario
    const fmtLocal = new Intl.DateTimeFormat(undefined, {
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
        hour12: false
    });

    function tick() {
        const now = new Date();
        elPeru.textContent = fmtPeru.format(now);
        elLocal.textContent = fmtLocal.format(now);
    }

    tick();
    setInterval(tick, 1000);
}